import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom'; // react-router-dom v6 사용
import './Login.css';

function Login({ toggleForm }) {
  const [team, setTeam] = useState('운영관리팀');
  const [id, setId] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleTeamChange = (newTeam) => {
    setTeam(newTeam);
  };

  const handleLogin = async () => {
    try {
      const response = await axios.get('/login', { // URL을 '/login'으로 수정
        params: {
          id,
          password,
          team
        }
      });

      if (response.data.success) {
        // 로그인 성공
        navigate('/map'); // navigate 사용
      } else {
        if (response.data.message === 'User not found') {
          alert('존재하지 않는 회원입니다');
        } else if (response.data.message === 'Incorrect password') {
          alert('비밀번호가 틀렸습니다');
        }
      }
    } catch (error) {
      console.error('Login error', error);
      alert('로그인 중 오류가 발생했습니다');
    }
  };

  return (
    <div className="login-container">
      <div className="logo">
        <img src="/path/to/logo.png" alt="로드가디언즈" /> {/* 이미지 경로 수정 */}
      </div>
      <div className="team-selector">
        <button
          className={team === '운영관리팀' ? 'active' : ''}
          onClick={() => handleTeamChange('운영관리팀')}
        >
          운영관리팀
        </button>
        <button
          className={team === '유지보수팀' ? 'active' : ''}
          onClick={() => handleTeamChange('유지보수팀')}
        >
          유지보수팀
        </button>
      </div>
      <div className="login-form">
        <div className="idbox">ID</div>
        <input 
          type="text" 
          placeholder="ID" 
          value={id}
          onChange={(e) => setId(e.target.value)}
        />
        <div className="psbox">Password</div>
        <input 
          type="password" 
          placeholder="Password" 
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <button onClick={handleLogin}>로그인</button>
        <div className="register-link">
          <span onClick={toggleForm}>회원가입</span>
        </div>
      </div>
    </div>
  );
}

export default Login;
